import { send } from 'emailjs-com';
import React, { useState } from 'react';
import { Card, Col, Container, Row } from "react-bootstrap";
import resdoc from "../assets/resume.pdf";
import resume from "../assets/resume.svg";
import "../styles/contact.css";
import "../styles/envelope.css";

const Skill = () => {
  const [flipCard, setFlipCard] = useState(false)
  const [toSend, setToSend] = useState({
    name: '',
    email: '',
    message: '',
    phone: '',
  });

  const handleSubmit = (e) => {
    console.log(toSend)
    e.preventDefault();
    send(
      'service_b4e93ui',
      'template_mstsg6b',
      toSend,
      'user_XFhpujPfYvhEq3RwTEbGU'
    )
      .then((response) => {
        console.log('SUCCESS!', response.status, response.text);
      })
      .catch((err) => {
        console.log('FAILED...', err);
      });
  };

  const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
  };


  return (
    <div className="Contact">

      <Container>
        <h1 className="contactheading">Contact</h1>
        <Row className="contactsection align-items-center"  >
          <Col md="4"  >
            <Card className="align-items-center  resumecard" style={{ marginLeft: "auto", marginRight: "auto" }} >
              <img className="resume" src={resume} alt="resume"></img>
              <a href={resdoc} target="_blank" class="btn" >View My Resume</a>
            </Card>
          </Col>
          <Col md="6" >

            {/* <div class="flip-card" on-click="toggle('flipCard')">{{ flipCard? 'Reset': 'Animate' }}</div> */}
            <div class="contact-wrapper">
              <div className={flipCard ? 'envelope active' : 'envelope'}>
                <div className="envelope">
                  < div class="back paper" ></div>
                  <div class="content">
                    <div class="form-wrapper">
                      <form>
                        <div class="top-wrapper">
                          <div class="input">
                            <label>Name*</label>
                            <input required type="text" onChange={handleChange} name="name" />
                          </div>
                          <div class="input">
                            <label>Phone</label>
                            <input type="text" onChange={handleChange} name="phone" />
                          </div>
                          <div class="input">
                            <label>Email</label>
                            <input type="text" onChange={handleChange} name="email" />
                          </div>
                        </div>
                        <div class="bottom-wrapper">

                          <div class="input">
                            <label>Message* </label>
                            <textarea required rows="3" onChange={handleChange} name="message"></textarea>
                          </div>
                          <div class="submit">
                            <div class="submit-card" onClick={(e) => { setFlipCard(true); handleSubmit(e) }}>Send Message</div>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                  <div class="front paper"></div>
                </div>
              </div>
            </div>
            <div class="smallform form-wrapper">
              <form>
                <div class="top-wrapper">
                  <div class="input">
                    <label>Name*</label>
                    <input required onChange={handleChange} type="text" name="name" />
                  </div>
                  <div class="input">
                    <label>Phone</label>
                    <input type="text" onChange={handleChange} name="phone" />
                  </div>
                  <div class="input">
                    <label>Email</label>
                    <input type="text" onChange={handleChange} name="email" />
                  </div>
                </div>
                <div class="bottom-wrapper">

                  <div class="input">
                    <label>Message*</label>
                    <textarea required rows="3" onChange={handleChange} name="message"></textarea>
                  </div>
                  <div class="submit">
                    <div class="submit-card" onClick={(e) => { setFlipCard(true); handleSubmit(e) }}>Send Message</div>
                  </div>
                </div>
              </form>
            </div>

          </Col>
        </Row>
      </Container >

    </div >
  );
};

export default Skill;
