import AOS from "aos";
import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import coda from "../assets/cert/coda.png";
import intern from '../assets/cert/intern.png';
import mern from "../assets/cert/mern.png";
import svce from "../assets/cert/SVCE.png";
import workshop from "../assets/cert/workshop.PNG";
import ztm from "../assets/cert/ztm.png";
import "../styles/cert.css";

const About = () => {
  AOS.init();


  return (
    <div className="Certificates">
      <Container style={{ paddingTop: "10vh" }}>
        <h1 className="certheading" >Certificates</h1>
        <Container>
          <Row style={{ textAlign: "center" }}>
            <Col data-aos="flip-right" data-aos-duration="1000"><img src={ztm} alt="ztm"></img></Col>
            <Col data-aos="flip-left" data-aos-duration="1000"><img src={mern} alt="mern"></img></Col>
            <Col data-aos="flip-right" data-aos-duration="1000"><img src={svce} alt="svce"></img></Col>
            <Col data-aos="flip-left" data-aos-duration="1000"><img src={workshop} alt="workshop"></img></Col>
            <Col data-aos="flip-right" data-aos-duration="1000"><img src={coda} alt="coda"></img></Col>
            <Col data-aos="flip-left" data-aos-duration="1000"><img src={intern} alt="intern"></img></Col>
          </Row>
        </Container>
      </Container>
    </div>
  );
};

export default About;
